.container {
  display: flex;
}

.wrap {
  width: 370px;

  border-left: 1px solid #e6ecf0;
  border-right: 1px solid #e6ecf0;
}

.dialog {
  width: 600px;
  border-right: 1px solid #e6ecf0;
}

.content {
  position: relative;

  height: calc(100vh - 117px);
  overflow: hidden;
}

.search {
  margin: 10px;
}
