.container {
  margin-bottom: -1px;
  transition-duration: 0.3s;
  border-top: 1px solid #e6ecf0;
  border-bottom: 1px solid #e6ecf0;
}

.container:hover,
.container:focus {
  background-color: #f5f8fa;
}

.container:focus {
  box-shadow: 0 0 0 2px #68ccfa inset;
}

.wrap {
  position: relative;

  padding: 10px;
  padding-left: 70px;
}

.avatar {
  position: absolute;
  left: 0;

  width: 50px;
  height: 50px;
  margin-left: 10px;
}

.header {
  display: flex;
}

.name,
.username,
.time {
  margin-right: 5px;
}

.text {
  margin-bottom: 10px;
  word-wrap: break-word;

  font-size: 15px;
}

.image {
  width: 100%;
  height: 290px;
  margin-bottom: 5px;

  border: 1px solid #e6ecf0;
  border-radius: 15px;

  overflow: hidden;
}

.btn {
  display: inline-flex;
  align-items: center;
  margin-right: 10px;

  cursor: pointer;
}

.btn_icon {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 36px;
  height: 36px;

  border-radius: 50%;
  transition-duration: 0.3s;
}

.value {
  color: #657786;
  font-size: 13px;
  transition-duration: 0.3s;
}

