.wrap {
  display: flex;
  flex-direction: column;
  padding-top: 5px;

  width: 275px;
  height: 100vh;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 50px;
  height: 50px;

  border-radius: 50%;
  transition-duration: 0.3s;
  cursor: pointer;
}

.logo:hover {
  background-color: #d0f0fe;
}

.logo:focus {
  box-shadow: 0 0 0 2px #68ccfa;
}

.btn {
  width: 196px;
  height: 49px;

  color: #ffffff;
  font-size: 15px;
  font-weight: 700;

  border-radius: 25px;
  background-color: #03a9f4;
  cursor: pointer;
  transition-duration: 0.3s;

  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

.btn:hover,
.btn:focus {
  background-color: #039ce1;
}
