.container {
  margin-bottom: 10px;
}

.list {
  list-style: none;
}

.link {
  display: inline-block;
  padding: 10px;
  margin: 2px 0;
  padding-right: 25px;

  font-size: 19px;
  font-weight: 700;
  border-radius: 25px;
  transition-duration: 0.3s;
}

.link:hover {
  color: #03a9f4;
  background-color: #d0f0fe;
  fill: #03a9f4;
}

.link:focus {
  color: #03a9f4;
  fill: #03a9f4;
  box-shadow: 0 0 0 2px #68ccfa;
}

.link_active {
  color: #03a9f4;
}

.icon {
  display: inline-block;
  width: 25.25px;
  height: 26.25px;
  margin-right: 20px;

  vertical-align: middle;
}

.icon_active {
  display: none;
}

.link_active .icon {
  fill: #03a9f4;
}

.link_active .icon_base {
  display: none;
}

.link_active .icon_active {
  display: inline-block;
}
